import {api} from '../api';

import {GrowattDataResponse, GrowattPlantData} from './types';

const getGrowattData = async ({
  user,
  password,
  options = {},
}: GrowattPlantData): Promise<GrowattDataResponse> => {
  try {
    const {data} = await api.post(
      `/alldata?user=${user}&password=${password}&options=${JSON.stringify(
        options,
      )}`,
    );
    return data as GrowattDataResponse;
  } catch (err) {
    throw new Error((err as Error).message);
  }
};

export {getGrowattData};
