import React, {useState} from 'react';

import DatePicker from 'react-date-picker';
import {useQuery} from 'react-query';
import {useLocation} from 'react-router-dom';
import {getGrowattData} from 'services/growatt/growatt';
import Loading from 'shared/components/Loading/Loading';

import {GrowattDataResponse} from '@services/growatt/types';

import {optionsDefault} from './types';

import * as S from './styles';

const Home: React.FC = () => {
  const [startDate, setStartDate] = useState<Date>();
  const [startDateConvert, setStartDateConvert] = useState<Date>();
  const [endDate, setEndDate] = useState<Date>();
  const [showInfo, setshowInfo] = useState<boolean>(false);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [searchTotal, setSearchTotal] = useState({start: 0, end: 0});
  const location = useLocation();
  const data = location.state.data as GrowattDataResponse;
  const keyPlanData = Object.keys(data.getAllPlantData)[0];
  const keyDeviceData = Object.keys(
    data.getAllPlantData[keyPlanData].devices,
  )[0];

  const {refetch: refetchStartData} = useQuery('getGrowattDataStart', {
    queryFn: () =>
      getGrowattData({
        user: location.state.user,
        password: location.state.password,
        options: {
          ...optionsDefault,
          historyLastStartDate: startDateConvert,
          historyLastEndDate: startDateConvert,
        },
      }),
    enabled: false,
    onSuccess: value => {
      setSearchTotal({
        start: value.getAllPlantData[keyPlanData].devices[keyDeviceData]
          .historyLast?.eacTotal as number,
        end: searchTotal.end,
      });
    },
  });

  const {refetch: refetchEndData} = useQuery('getGrowattDataEnd', {
    queryFn: () =>
      getGrowattData({
        user: location.state.user,
        password: location.state.password,
        options: {
          ...optionsDefault,
          historyLastStartDate: endDate,
          historyLastEndDate: endDate,
        },
      }),
    enabled: false,
    onSuccess: value => {
      setSearchTotal({
        start: searchTotal.start,
        end: value.getAllPlantData[keyPlanData].devices[keyDeviceData]
          .historyLast?.eacTotal as number,
      });
      setIsLoading(false);
      setshowInfo(true);
    },
  });

  const handleSearch = () => {
    setshowInfo(false);
    setIsLoading(true);
    refetchStartData();
    refetchEndData();
  };

  return (
    <S.Container>
      <S.Title>Growatt Analyzer</S.Title>
      <S.Card>
        <S.Text>
          Total Gerado{' '}
          <b>
            {JSON.stringify(
              data.getAllPlantData[keyPlanData].devices[keyDeviceData]
                .historyLast?.eacTotal,
            )}{' '}
            kWh
          </b>
        </S.Text>
      </S.Card>
      <S.SubTitle>Pesquisar por intervalo</S.SubTitle>
      <S.Card>
        <S.Descrption>Data Inical </S.Descrption>
        <DatePicker
          required
          onChange={(value: unknown) => {
            const date = new Date(value as string);
            const day = date.getDate() - 1;
            date.setDate(day);
            setStartDateConvert(date);
            setStartDate(new Date(value as string));
          }}
          value={startDate}
        />
        <S.Descrption>Data Final </S.Descrption>
        <DatePicker required onChange={setEndDate} value={endDate} />
        <S.Button
          disabled={
            !startDate ||
            !endDate ||
            !(((startDate as Date) && (endDate as Date)) <= new Date())
          }
          onClick={handleSearch}>
          Buscar
        </S.Button>
        {isLoading && <Loading />}
        {showInfo && searchTotal.end && searchTotal.start && (
          <S.Info>
            {(searchTotal.end - searchTotal.start).toFixed(1)} kWhs
          </S.Info>
        )}
      </S.Card>
    </S.Container>
  );
};
export default Home;
